import React from "react";
import { useState, useEffect, useRef} from "react";
import { json, checkStatus } from "./utils";
import Dropdown from 'react-dropdown';
import * as icons from 'react-bootstrap-icons';
import 'react-dropdown/style.css';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Chart from "chart.js";


  
const Convert = () => {

    const [currencyRates, setCurrencyRates] = useState([]);
    const [currencyFrom, setCurrencyFrom] = useState("USD");
    const [currencyTo, setCurrencyTo] = useState("EUR");
    const [currencies, setCurrencies] = useState([]);
    const [fromValue, setFromValue] = useState(1);
    const [toValue, setToValue] = useState(1);
    const chartRef = useRef();

    useEffect(() => {
        fetch(`https://api.frankfurter.app/latest?from=USD`)
            .then(checkStatus)
            .then(json)
            .then((data) => {
                setCurrencyFrom("USD");
                setCurrencyTo("EUR");
                setCurrencyRates(data.rates);
                setFromValue(1);
                setToValue(1);
                setCurrencies(Object.keys(data.rates));
            });
        convertFromTo();
            
    }, []);
    
    
    function getHistoricalRates (currencyFrom, currencyTo, chartRef) {
        const endDate = new Date().toISOString().split('T')[0];
        const startDate = new Date((new Date).getTime() - (30 * 24 * 60 * 60 * 1000)).toISOString().split('T')[0];
    
        fetch(`https://api.frankfurter.app/${startDate}..${endDate}?from=${currencyFrom}&to=${currencyTo}`)
          .then(checkStatus)
          .then(json)
          .then(data => {
            if (data.error) {
              throw new Error(data.error);
            }
    
            const chartLabels = Object.keys(data.rates);
            const chartData = Object.values(data.rates).map(rate => rate[currencyTo]);
            const chartLabel = `${currencyFrom}/${currencyTo}`;
            buildChart(chartLabels, chartData, chartLabel);
          })
          .catch(error => console.error(error.message));
      }

    function buildChart(labels, data, label) {
        const chart = new Chart(chartRef.current.getContext('2d'), {
            type: 'line',
            data: {
                labels,
                datasets: [
                    {
                        label: label,
                        data,
                        fill: false,
                        tension: 0,
                    }
                ]
            },
            options: {
                responsive: true,
            }
        });
    }

    
    useEffect(() => {
        fetchCurrency();
    }, [currencyFrom, currencyTo]);

    function fetchCurrency() {
        fetch(`https://api.frankfurter.app/latest?from=${currencyFrom}`)
        .then(checkStatus)
        .then(json)
        .then((data) => {
            setCurrencyRates(data.rates);
        })
    }

    useEffect(() => {
        const baseOptions =  Object.keys(currencyRates);
        setCurrencies(baseOptions);
    }, [currencyFrom]);

    useEffect(() => {
        convertFromTo();
    }, [fromValue, currencyFrom, currencyTo]);

    useEffect(() => {
        convertToFrom();
    }, []);
    function convertFromTo() {
        const rate = currencyRates[currencyTo];
        const convertedValue = fromValue * rate;
        setToValue(convertedValue.toFixed(2));
    }

    useEffect(() => {
        getHistoricalRates(currencyFrom, currencyTo, chartRef);
    }, [currencyFrom, currencyTo, chartRef]);


    function convertToFrom() {
        const rate = currencyRates[currencyTo];
        const convertedValue = toValue / rate;
        setFromValue(convertedValue.toFixed(2));
    }

    function handleCurrencyFromChange(option) {
        setCurrencyFrom(option.value);
        fetchCurrency();
        convertFromTo();
        getHistoricalRates();
    }

    function handleCurrencyToChange(option) {
        setCurrencyTo(option.value);
        fetchCurrency();
        convertFromTo();
        getHistoricalRates();
    }

    function reverse () {
        setCurrencyTo(currencyFrom);
        setCurrencyFrom(currencyTo);
        setFromValue(toValue);
        
    }

    function handleInputFromChange (value) {
        setFromValue(value);
        convertFromTo();
    }

    function handleInputToChange (value) {
        setToValue(value);
        convertToFrom();
    }


    return (
        <div>
            <div className="rowC">
                <div>
                    <Dropdown
                    options={currencies}
                    value={currencyFrom}
                    onChange={handleCurrencyFromChange}
                    />
                    <input value={fromValue} id='from' onChange={(e) => handleInputFromChange(e.target.value)} type="number"/>
                </div>
                <div>
                    <Dropdown
                    options={currencies}
                    value={currencyTo}
                    onChange={handleCurrencyToChange}
                    />
                      <input value={toValue} id='to' onChange={(e) => handleInputToChange(e.target.value)} type="number"/>
                </div>
            </div>

            <button type="button" className="btn btn-outline-secondary mb-2" onClick={reverse}>
                <icons.ArrowLeftRight/>
            </button>
            <Tabs>
                <TabList>
                    <Tab>
                        Conversion Table for { currencyFrom }
                    </Tab>
                    <Tab onClick={() => getHistoricalRates(currencyFrom, currencyTo, chartRef)}>
                        Historical { currencyFrom }/{ currencyTo} Rates
                    </Tab>
                </TabList>
                <TabPanel>
                <div className="container">
                    <h5>Conversion Table for {currencyFrom}</h5>
                    <div className="conversionTableContainer"> 
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Currency</th>
                                    <th>Rate</th>
                                </tr>
                            </thead>
                            <tbody>
                                {Object.entries(currencyRates).map(([currency, rate]) => (
                                    <tr key={currency}>
                                        <td>{currency}</td>
                                        <td>{rate}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
                </TabPanel>
                <TabPanel>
                    <canvas ref={chartRef} />
                </TabPanel>
            </Tabs>
        </div>
    )
}

export default Convert;